import React from 'react'
import CSwiper from 'crash-swiper'
import arrow from './arrow.png'
import './swiper.css'

function Swiper(props) {
    let [swiper, setSwiper] = React.useState(null)
    React.useEffect(() => {
        let swiper = new CSwiper('.common-swiper', props.option)
        setSwiper(swiper)
    }, [])
    return (
        <div className={`common-swiper ${props.name}`}>
            <div className='btn'>
                <div className='leftBtn' onClick={() => { swiper.onNext() }}>
                    <img src={arrow} />
                </div>
                <div className='rightBtn' onClick={() => { swiper.onPrev() }}>
                    <img src={arrow} />
                </div>
            </div>
        </div>
    )

}

export default Swiper